* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  /* font-family: "Poppins-Medium"; */
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: unset;
}
a:hover {
  color: unset;
}
.cursor-pointer {
  cursor: pointer;
}
section {
  margin: 3rem 0rem;
}
